<template>
  <div
    v-if="clz"
    class="detail ignore"
  >
    <div class="detail-content">
      <template v-for="(component, index) in components">
        <component
          :is="getComponent(component)"
          :key="index"
          :clz="clz"
          class="lm-1px-b"
        />
      </template>
    </div>
  </div>
</template>

<script>
import mixins from './mixins'
export default {
  name: 'ClassroomDetail',
  mixins: [mixins],
  data () {
    return {
      clz: {},
      components: ['video-cover', 'header', 'content', 'tab','footer']
    }
  },
  mounted () {
    this.clz = this.$route.params.clz
    const classId = this.$route.query.classId
    if (!this.clz && classId) {
      this.getTopTeacherClassList()
    }
  },
  methods: {
    getComponent (component) {
      return `clz-${component}`
    }
  }
}
</script>

<style lang="less" scoped>
.detail.ignore {
  overflow: hidden;
  background-color: #fff;
  padding-bottom: 50px;
}
</style>
