<template>
  <div class="clz-lesson">
    <div class="clz-lesson-msg secondary-font">课时简介</div>
    <template v-for="(clz, index) in lessons">
      <div
        :key="index"
        class="clz-lesson-box lm-1px-b"
      >
        <div class="clz-lesson-box-title primary-font">
          {{ `第 ${(index+1)*2-1} - ${(index+1)*2} 课时： ${clz.LessonTitle}` }}
        </div>
        <div class="clz-lesson-box-desc content-font">
          课时描述 {{ clz.LessonDescription }}
        </div>
        <div class="clz-lesson-box-date content-font">
          授课时间：{{ clz.BeginDate | formatMD }} —— {{ clz.EndDate | formatHS }}
        </div>
        <div class="clz-lesson-box-ft">
          <span class="clz-lesson-box-ft-status">
            {{ clz.StatusName }}
          </span>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import create from '../../../mixins/create'
export default {
  mixins: [create],
  data () {
    return {
      lessons: [],
      footer: {}
    }
  },
  mounted () {
    this.getClassLesson()
  },
  methods: {
    getClassLesson () {
      this.$store
        .dispatch('getClassLesson', this.clz.ClassID)
        .then(data => {
          this.lessons = data
        })
        .catch(() => {})
    }
  }
}
</script>

