<template>
  <div class="img-content">
    <video-player
      v-show="canPlay"
      ref="videoPlayer"
      :options="playerOptions"
      :playsinline="true"
      class="video-player vjs-custom-skin"
    />
    <img
      v-show="!canPlay"
      :src="clz.CoverPath"
      height="150"
      width="100%"
      style="object-fit: cover;"
      class="img-content-img"
    >
  </div>
</template>

<script>
import create from '../mixins/create'
import { videoPlayer } from 'vue-video-player'
export default {
  components: { videoPlayer },
  mixins: [create],
  data () {
    return {
      canPlay: true
    }
  },
  computed: {
    player () {
      return this.$refs.videoPlayer && this.$refs.videoPlayer.player
    },
    playerOptions () {
      return {
        muted: false,
        fluid: true,
        loop: false,
        preload: 'auto',
        language: 'zh-CN',
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: 'video/mp4',
            src: this.clz.ClassicCourseUrl
          }
        ],
        aspectRatio: '16:9',
        poster: this.clz.CoverPath,
        height: 150,
        notSupportedMessage: '此视频暂无法播放，请稍后再试'
      }
    }
  },
  mounted () {
    this.player.on('error', () => {
      this.player.off('error')
      this.canPlay = false
    })
  }
}
</script>

<style lang="less" scoped>
.img-content {
  width: 100%;
  &-img {
    object-fit: cover;
  }
}
