<template>
  <div class="clz">
    <div class="clz-detail">
      <div class="clz-detail-msg msg-font">课程简介</div>
      <div class="clz-detail-desc content-font">
        课堂描述{{ clz.Describe }}
      </div>
      <div class="clz-detail-msg msg-font">报班须知</div>
      <ul class="clz-detail-rules">
        <li class="clz-detail-rules-title">授课须知
          <div
            v-if="rules.TeachingRule"
            class="clz-detail-rules-content content-font"
            v-html="trim(rules.TeachingRule)"
          />
        </li>
        <li class="clz-detail-rules-title">退班规则
          <div
            v-if="rules.QuitRule"
            class="clz-detail-rules-content content-font"
            v-html="trim(rules.QuitRule)"
          />
        </li>
        <li class="clz-detail-rules-title">插班规则
          <div
            v-if="rules.TransferRule"
            class="clz-detail-rules-content content-font"
            v-html="trim(rules.TransferRule)"
          />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { trim } from '@/utils'
import create from '../../../mixins/create'
export default {
  mixins: [create],
  data () {
    return {
      rules: []
    }
  },
  mounted () {
    this.getSysRules()
  },
  methods: {
    getSysRules () {
      this.$store
        .dispatch('getSysRules')
        .then(data => {
          this.rules = data
        })
        .catch(() => {})
    },
    trim (str) {
      return trim(str)
    }
  }
}
</script>

