<template>
  <div class="footer ignore">
    <div class="footer-bt">
      <van-button
        type="primary"
        size="normal"
        @click="sign"
      >报名</van-button>
    </div>
  </div>
</template>

<script>
import create from '../mixins/create'
import { appUrl } from '@/utils';
export default {
  mixins:[create],
  methods: {
    sign() {
      if (this.userInfo) {
        this.signClass();
      } else {
        this.$dialog
          .confirm({
            title: "登录提示",
            message: "报名课堂，需要登录龙门课堂？",
            messageAlign: "left"
          })
          .then(() => {
            this.login()
          })
          .catch(() => {
            // on cancel
          });
      }
    },
    signClass() {
      this.$store
        .dispatch("topClassSignIn", {
          ClassID: this.clz.ClassID,
          RefererCode:
            this.$route.query.RefererCode || this.clz.RefererCode || ""
        })
        .then(data => {
          this.$toast(data)
          this.showConfirm()
        })
        .catch(() => {
          this.showConfirm()
        });
    },
    showConfirm () {
      this.$dialog
          .confirm({
            title: "报名成功",
            message: "是否马上下载课鱼时间APP去上课？",
            messageAlign: "left"
          })
          .then(() => {
            window.open(appUrl)
          })
          .catch(() => {
            // on cancel
          });
    }
  }
};
</script>

<style lang="less" scoped>
.footer.ignore {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.footer-bt {
  text-align: right;
}
</style>
